<template>
	<div class="_full_router fund-manage-con">
			<custHeader :title="title"></custHeader>
			<div class="fund-manage-contain form-submit">
				<div v-show="flowId==1">
                <mt-field label="新密码" placeholder="请输入登录密码" v-model="password" type="password"  v-validtype="rules.password" :state="rules.password.state"></mt-field>
                <mt-field label="新密码" placeholder="请输入登录密码" v-model="password2" type="password"  v-validtype="rules.password2" :state="rules.password2.state"></mt-field>
				<mt-button  type="primary" size="large"  @click="setPassword" :disabled="valiSuccess">提交</mt-button>
				<!-- <mt-button  type="primary" size="large"  @click="showFlow(1)">上一步</mt-button> -->
				</div>
        </div>
	</div>
</template>
<script>
import {Field,Picker,Popup,MessageBox,Actionsheet,Toast} from 'mint-ui'
import custHeader from  'components/custHeader'
import zjz from  'assets/2276.png'

Vue.component(Actionsheet.name,Actionsheet);
Vue.component(Field.name,Field)
Vue.component(Popup.name,Popup)
Vue.component(Picker.name,Picker)
export default{
	data(){
		return {

			picMsg:"",
			isNotChecked:false,
			dicts:{
                idType:{
                    name:'ID_TYPE',
                    dataKey:"selectTypeSlots"
                }
		},
			actions:[{name:"拍照",
						method:()=>{
							this.selectPic();
						}
						},
						{name:"从相册选择",
						method:()=>{
							this.selectPic();
						}
						}
			],
			sheetVisible:false,
			zjzshow:false,
			zjz:zjz,
		flowId:"1",
		title:"找回密码",
        capMessage: '发送短信',

    mb:'',
    captcha:'',
    password:'',
    password2:'',
    rules:{
      password:{
        str:"required",
        state:''
      },
       password2:{
        str:"required,equals[password]",
        state:'',
        message:''
      },
    }

		}
	},
	created:function(){
	//	this.qryCustBaseInfo();
	if(this.$route.query.mb){
		this.mb=this.$route.query.mb;
	}
	},
	 components:{
    custHeader
  },
  computed:{
  	valiSuccess:function(){
  		return (this.mb!="" && this.password!="" && this.password2!="")? false:true;
  	},
  },
	methods:{
        setPassword:function(){
        	if(this.rules.password2.state=="error"){
        		 Toast({
	            message: this.rules.password2.message,
	            position: "bottom",
	            duration: global.TOASTTIME
	          });
        		return ;
        	}

        	

            if(global.isOpenMd5){
              this.password=global.md5(this.password);
            }
            var pars={
                password:this.password,
                useScope:global.USE_SCOPE.LOGINPASSWORD,// 0登录/交易1资金业务
                authType: global.AUTH_TYPE,
                mobilePhone:this.mb
            }
            axiosUtil.upCltPasswordBack(pars).then(data=>{
                var data=data.data;
                if(data.errorcode==0){
                    	// MessageBox("提示","重置成功")
                    	MessageBox.alert("重置成功").then(action=>{
                    		this.$router.push("/login");
                    	})
                }else{
                    	MessageBox("提示",data.errormsg)
                }
            })
        }
	
	}
}

</script>
<style scoped>
.fund-manage-con{

}
.fund-manage-contain{
	padding: 12px;
	overflow-y:auto;
	    height: 100%;
    padding-bottom: 48px;
}
.select-dict{
	font-size: 14px;
}
	.select-dict button{
		    width: 50%;
    float: left;
    height: 28px;
    border: none;
    background: #fff;
    border-top: 1px solid #ccc;
	}
	.select-dict button:last-child{
		border-left: 1px solid #ccc;
	}
	.select-dict-btns{
		position: absolute;
		bottom: 0;
		bottom: -28px;
    width: 100%;
	}
	.zjz{
		width: 100%;
	}
</style>



